}

.margin-y-low {
    margin-top: 5px;
    margin-bottom: 5px;
}

.centered {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.centered-radio {
    display: flex;
    flex-direction: row;
    width: 40%;
    justify-content: center;
    margin-top: 10px;
}
 
.mb-1 {
    margin-bottom: 10px
}
.bottom-question-container {
    display: flex;
    flex-direction: row;
    padding: 0px;
    gap: 29px;
    justify-content: center;
    width: 100%;
    height: 131px;
    /* Inside auto layout */

    flex: none;
    order: 1;
    flex-grow: 0;
}

.full-length-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.radio-and-input-container {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 151px;
    padding: 50px;
    width: auto;

}

.auto-grow {
